
import React from 'react'

function RecipeCard({lable,image,url}) {
    const gotoWeb=()=>{
        window.open(url);
    }
    return (
        <div className="col-md-6 my-2 d-flex justify-content-center">
            <div onClick={gotoWeb} class="card" style={{width: "18rem"}}>
                <img src={image} class="card-img-top" alt="..." />
                <div class="card-body">
                    <h5 class="card-title">{lable}</h5>
                </div>
            </div>
        </div>
    )
}

export default RecipeCard
