import {useState} from 'react';
import axios from 'axios'
import './App.css';
import RecipeCard from './RecipeCard';

function App() {
  const YOUR_APP_ID="06ac0c5c";
  const YOUR_APP_KEY="f98d0ff4ddaf13b6972f4bf5fb0b748d";
  
  const [inputrecipe, setInputrecipe] = useState('');
  const [recipefre,setRecipefre]=useState('alcohol-free');
  const [allrecipe,setAllrecipe]=useState([]);
  const url=`https://api.edamam.com/search?q=${inputrecipe}&app_id=${YOUR_APP_ID}&app_key=${YOUR_APP_KEY}&health=${recipefre}`
  const onSearch=async()=>{
      const res=await axios.get(url);
      console.log(res.data);
      console.log(res.data.hits)
      setAllrecipe(res.data.hits)
  }
  return (
    <div className="App">
      <div className="container">
      <h1 className="text-center">Arbaz Food Recipe üçï</h1>
      <div className="mt-4" >
        <div className="row">
          <div className="col-md-4">
            <input type="text" class="form-control" value={inputrecipe} onChange={(e)=>setInputrecipe(e.target.value)}  placeholder="Enter recipe name"  />
          </div>
          <div className="col-md-4 d-flex justify-content-center">
              <button className="btn btn-primary" onClick={onSearch}>Search</button>
          </div>
          <div className="col-md-4">
            <select  class="form-select" aria-label="Default select example">
              <option value='alcohol-free' onClick={(e)=>setRecipefre(e.target.value)}>alcohol-free</option>
              <option value='egg-free' onClick={(e)=>setRecipefre(e.target.value)}>egg-free</option>
              <option value='tree-nut-free' onClick={(e)=>setRecipefre(e.target.value)}>tree-nut-free	</option>
              <option value='fish-free' onClick={(e)=>setRecipefre(e.target.value)}>fish-free</option>
              <option value='vegan' onClick={(e)=>setRecipefre(e.target.value)}>vegan</option>
              <option value='vegetarian' onClick={(e)=>setRecipefre(e.target.value)}>vegetarian</option>
              <option value='sugar-conscious'  onClick={(e)=>setRecipefre(e.target.value)}>sugar-conscious</option>
            </select>
          </div>
        </div>
      </div>
      <div className=" mt-5 ">
        <div className="row">
          {
            allrecipe.map((data)=>(
              <RecipeCard lable={data.recipe.label} image={data.recipe.image} url={data.recipe.url} />
            ))
          }
        </div>
      </div>
      </div>
    </div>
  );
}

export default App;



        
        